	module.exports = {"data":{"post":{"html":"<p>I've got poor priorities for shooting video. Instead of taking silly videos of my wife, I should have been filming Christopher this afternoon as he was doing laps around the backyard on his bike, the first time without training wheels. He was pretty happy with himself. That he wasn't until now able to keep up with the neighbor kids in the bike-riding department was a sore point with him. We tried a couple of times last year, but he kept asking for the training wheels to go back on. Today he crashed a few times in the grass, but laughed at himself and kept going.</p>\n<p>Another sign of his improved coordination and self-confidence came a few weeks ago, when he insisted we take him swimming so he could show us what he could do in the deep water.  He's never wanted to even get his head wet before, but now he's going in water over his head with inflatable water wings on his arms (is that really what <em>Schwimmflügel</em> are called in English?) and doing cannonballs.</p>\n<p>We really have to thank his kindergarten for this. They go swimming in groups of 3 every few weeks, and I also know they've been having Christopher practice with a walking bike outside. He's also been doing an hour of therapeutic tumbling every week, paid for by health insurance. Now after <a href=\"http://www.papascott.de/archives/2004/08/05/1st-day-kindergarten/\">two years</a>, he just four days left. Then, after summer vacation, he starts public school. I'm sure bravery and balance will come in handy there.</p>\n<p>We'll do some more bike laps tomorrow afternoon, I'll try to remember to bring the camera.</p>","fields":{"title":"Bravery and Balance","date":"16 Jul 2006","url":"https://papascott.de/2006-07-16-bravery-and-balance/"}}},"pathContext":{"slug":"/2006-07-16-bravery-and-balance/","prev":{"fields":{"title":"Firestarter","date":"15 Jul 2006","slug":"/2006-07-15-firestarter/"}},"next":{"fields":{"title":"And Action!","date":"17 Jul 2006","slug":"/2006-07-17-and-action/"}}}}

